.catalog {
  margin: 0 20px;

  @media (min-width: $tablet-width) {
    margin: 0 30px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    margin: 74px auto 0 auto;
  }

  &--extra {
    margin: 0 20px;

    @media (min-width: $tablet-width) {
      margin: 0 30px;
    }

    @media (min-width: $desktop-width) {
      position: relative;
      width: 1220px;
      margin: 0 auto;
      margin-bottom: 60px;
      display: flex;
      justify-content: space-between;
    }
  }
}

.catalog__heading {
  margin: 0;
  margin-bottom: 50px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}


.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 60px;
    column-gap: 60px;
    margin-bottom: 68px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.catalog__item {
  text-align: center;
  margin-bottom: 33px;
}
